.header {
  padding: 46px 0 56px;
}

.header__row {
  display: flex;
  column-gap: 30px;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
display: flex;
align-items: center;
column-gap: 13px;

}

.header__logo span {
  font-weight: 900;
  font-size: 40px;
  line-height: 1.5;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}

.header__nav {
  font-weight: 700;
  font-style: 22px;
  line-height: 1.5;
  letter-spacing: 0.01em;
}

.header__nav ul {
display: flex;
column-gap: 65px;
align-items: center;

}

.header__nav a {
  color: var(--textColor);
}

a.header__nav-btn {
  padding: 13px 22px 17px;
  background-color: #000;
  border-radius: 7px;
  color: #fff;
  font-style: 24px;
  line-height: 1.5;
  letter-spacing: -0.03em;
}